
.color-slider {
  position: relative;
  height: 10px;
  border-radius: 2px;
  background: url(../../../../assets/img/alpha_bg.png);
}
.color-slider .pointer {
    position: absolute;
    width: 14px;
    height: 14px;
    top: -2px;
    cursor: pointer;
    margin-left: -7px;
    border-radius: 50%;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
    background: #f5f5f5;
}



