
.color-saturation {
  position: relative;
  height: 120px;
  cursor: default;
}
.color-saturation::before, .color-saturation::after {
    position: absolute;
    content: '';
    display: block;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
.color-saturation::before {
    background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0));
}
.color-saturation::after {
    background: linear-gradient(to top, #000, transparent);
}
.color-saturation .pointer {
    position: absolute;
    width: 10px;
    height: 10px;
    margin-top: -5px;
    margin-left: -5px;
    border-radius: 50%;
    border: 1px solid #fff;
    z-index: 2;
}



