
.element-countdown {
}
.element-countdown-content {
  display: flex;
  flex-direction: row;
}
.countdown-item {
  text-align: center;
  flex: 1;
}
.countdown-seperator {
  text-align: center;
  min-width: 18px;
  display: flex;
  flex-direction: column;
}
.timer-seperator {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
}
.timer-label {
  display: block;
  height: 16px;
  margin-bottom: 8px;
}
.timer-time {
  background: #eee;
  font-size: 20px;
  padding: 8px;
  border-radius: 4px;
}



