
.sidebar {
  position: fixed;
  z-index: 102000;
  width: 300px;
  top: 50px;
  right: 0;
  height: calc(100% - 50px);
  background-color: #fff;
  font-size: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  box-sizing: content-box;
}
.sidebar-header {
  background: #f6f6f6;
  padding: 10px;
  height: 55px;
  border-bottom: 1px solid #ddd;
}
.sidebar-body {
  padding: 0 12px;
  height: calc(100% - 54px);
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
}
.sidebar-enter-active, .sidebar-leave-active {
  transition: all .3s;
}
.sidebar-enter, .sidebar-leave-active {
  right: -300px;
}
.sidebar-block {
  padding: 15px 0;
}
.sidebar-block .text-input {
  width: 100%;
  padding: 4px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.sidebar-block-inside {
  padding-top: 10px;
}



