
.date-picker-wrap.btn {
  position: relative;
}
.date-picker-wrap.btn .date-picker {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    padding: 0;
}
.date-picker-wrap.btn .date-picker input {
      cursor: inherit;
      padding: 0;
      height: 100%;
}
.date-picker-wrap.btn .date-picker .el-icon-date {
      display: none;
}



