.chartist-tooltip {
  position: absolute;
  display: inline-block;
  min-width: 5em;
  padding: 10px 15px;
  font-size: .8em;
  background: #37475a;
  color: #fff;
  pointer-events: none;
  opacity: 0;
  transition: all .2s ease;
  border-radius: 2px;
  z-index: 2;
  white-space: nowrap;
}
.chartist-tooltip:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    width: 0;
    height: 0;
    margin-left: -6px;
    border: 6px solid transparent;
    border-top-color: #37475a;
}
.chartist-tooltip.tooltip-show {
    opacity: 1;
}
.chartist-tooltip .tooltip-title {
    text-align: center;
    color: #fff;
    margin-bottom: 5px;
}
.chartist-tooltip .tooltip-title::before {
      width: 12px;
      height: 12px;
}
.chartist-tooltip .tooltip-title span {
      vertical-align: middle;
}
.chartist-tooltip .tooltip-data-list {
    list-style: none;
    padding: 0;
}
.ct-area, .ct-line {
  pointer-events: none;
}
.ct-legend-list {
  padding: 10px;
  text-align: center;
}
.ct-legend, .tooltip-title {
  display: inline-block;
  margin: 0 10px;
  cursor: pointer;
  line-height: 16px;
  font-weight: normal;
  margin-left: 5px;
  color: #999;
  font-size: 13px;
}
.ct-legend::before, .tooltip-title::before {
    display: inline-block;
    content: '';
    width: 16px;
    height: 16px;
    vertical-align: middle;
    margin-right: 5px;
}
.ct-legend.is-disable, .tooltip-title.is-disable {
    text-decoration: line-through;
}
.ct-legend.is-disable::before, .tooltip-title.is-disable::before {
      background: rgba(0, 0, 0, 0.1);
}
